import { AmountContacts, ContactsContainer, Container, EmptyText, MainTitle, Title } from 'components/App/App.styled';
import { ContactForm } from 'components/ContactForm';
import { Contacts } from 'components/Contacts';
import { Error } from 'components/Error/Error';
import { Filter } from 'components/Filter';
import { Loader } from 'components/Loader/Loader';
import { useSelector } from 'react-redux';
import { useFetchContactsQuery } from 'redux/Contacts/contactsApi';
import { selectFilter } from 'redux/selectors';

export default function ContactsPage() {
  const {
        data: contacts = [],
        isFetching: isLoading,
        error,
      } = useFetchContactsQuery();
    
      const filter = useSelector(selectFilter);

      const getFilteredContacts = () => {
        const normalizedFilter = filter.toLowerCase();
        return contacts.filter(contact =>
          contact.name.toLowerCase().includes(normalizedFilter)
        );
      };
    
      const visibleContacts = getFilteredContacts();
    
  return (
    <Container>
       <MainTitle>–¢–µ–ª–µ—Ñ–æ–Ω–Ω–∞ –∫–Ω–∏–≥–∞</MainTitle>
       {isLoading && !error && <Loader />}
      <ContactForm />
      <ContactsContainer>
      {error ? (
    <Error>{error}</Error>
  ) : (
    <div>
      <Title>–ú–æ—ó –∫–æ–Ω—Ç–∞–∫—Ç–∏</Title>
      <AmountContacts>
        –ó–∞–≥–∞–ª—å–Ω–∞ –∫—ñ–ª—å–∫—ñ—Å—Ç—å –∫–æ–Ω—Ç–∞–∫—Ç—ñ–≤: {contacts.length}
      </AmountContacts>
      <Filter />
      {visibleContacts.length ? (
        <Contacts contacts={visibleContacts} />
      ) : (
        <EmptyText>–ù–µ –∑–Ω–∞–π–¥–µ–Ω–æ –∂–æ–¥–Ω–æ–≥–æ –∫–æ–Ω—Ç–∞–∫—Ç—É</EmptyText>
      )}
    </div>
  )}
      </ContactsContainer>
    </Container>
  );
}

